
.App {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
}


.Kapitola-gray {
  font-weight: 900;
  font-size: 35px;
  color:white;
  background-color: gray;
  margin: 0px;
  padding: 15px;
  /* height: 55px; */
  display: flex;
  justify-content: center;
  align-items: center;
} 

.Kapitola-yellow {
  font-weight: 900;
  font-size: 35px;
  color:black;
  background-color: yellow;
  margin: 0px;
  padding: 15px;
  /* height: 55px; */
  /* display: flex; */
  justify-content: center;
  align-items: center;
} 

/* O nás */

.Images {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.Logos {
  margin: 10px;
  max-width: 200px;
  max-height: 100px;
}

.Text {
  font-weight: 400;
  color: white;
  background-color: gray;
  margin: 0;
}

/* Skupiny karet */

.CardGroup {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.card-footer.yellow {
  background-color: yellow;
}

.card-footer.gray {
  background-color: gray;
}



.BombCardGroup{
  width: 30%;
  min-width: 400px;
  margin: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
  box-shadow: 5px 5px 20px rgba(100, 100, 100, 0.7);
  align-self: stretch;
  border: none;
}

/* navbar start */

#basic-navbar-nav, #navbar-collapse, #collapse  
{
  /* position: absolute; */
  /* right: 1em; */
}

.navbar-light, .navbar-brand {
  font-weight: 900;
  font-size: 22px;
  /* color: pink; */
}

.bg-dark {
  background-color: yellow !important;
}

.cenik-img {
  width: 100%;
  object-fit: cover;
}

/* navbar end */

h1 {
  font-weight: 900;
  font-size: 35px;
}

h3 {
  font-weight: 900;
  font-size: 25px;
}

p {
  font-weight: 400;
  font-size: 20px;
  padding: 0 10% ;
}

li ul {
  font-weight: 400;
  font-size: 20px;
  padding: 0 10%;
}

.main-bg{
  width: 100%;
  background-image: url("../src/images/Sharpening_cut_1920.png");
  background-repeat: no-repeat;
  background-size: cover;
}

#bg-info {
  padding-top: 10%;
  padding-bottom: 10%;
  margin-left: 5%;
  text-align: left;
  /* height: 100%; */
}

#main-logo {
  min-width: 300px;
  width: 30%;
  margin: 3%;
}

#main-list {
  font-weight: 900;
  font-size: 25px;
  color: black;
}


/* Bomičk */

.card-body {
background-color: orange;
display: flex;
flex-flow: column;
}

.CardInfo {
  display: flex;
}

.BombText {
  color: white;
  text-align: left;
  margin: 0;
}

.BombCard {
  width: 40%;
  margin: auto;
  margin-top: 20px;
  margin-bottom: 20px;
  box-shadow: 5px 5px 20px rgba(100, 100, 100, 0.7);
}


.BombCardImg {
  max-height: 270px;
  /* min-width: 100px; */
  margin: auto;
  background-color: red;
  position: relative;
  object-fit: cover;
}

.BombCardBut {
  background-color: white;
  color: orange;
  margin: 20px;
  border: none;
  border-radius: 0;
  font-size: 20px;
  font-weight: 900;
  padding: 11px 35px;
}

.BombCardBut:hover, .BombCardBut:focus {
  background-color: grey;
  color: orange;
}

.BombCardBut:active:after:not() {
  background-color: hotpink;
}

.BombModal {
  background-color: rgba(50, 50, 50, 0.7);
  text-align: center;
  
}

.BombModal p {
  text-align: justify;
}

.BombModalTitle {
  background-color: orange;
  color: white;
}

.gray-background {
  background-color: gray;
  color: white;
}

.yellow-background {
  background-color: yellow;
  color: black;
}

.white-color {
  color: white;
}

.black-color {
  color: black;
}

:target {
  display: block;
  position: relative;
  top: -58px; 
  visibility: hidden;
}

.Kontakty {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-wrap: wrap;
  text-align: left;
}

.Kontakty p {
  margin: 0px 0px;
  padding: 0px;
}

.Kontakty div {
  padding: 10px 30px;
}

#box1 {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

#box1 a {
  color: white;
  font-size: 20px;
}

#box2 {
  text-align: center;
}

#contact-title {
  margin: 0;
  padding: 0;
}


#mapa {
  background-color:gray;
  height: 300px;
}

#ahoj p {
  margin: 0px;
  height: 100%;
}

.dropdown-item.active {
  background-color: gray;
}

.dropdown-item:focus {
  background-color: gray;
}

